import { extend } from "vee-validate";
import * as rules from "vee-validate/dist/rules";

Object.keys(rules).forEach((rule) => {
  extend(rule, rules[rule]);
});

// Override the default message.
extend("email", {
  ...rules.email,
  message: "  ",
});

extend("required", {
  ...rules.required,
  message: "   ",
});

extend("max", {
  ...rules.max,
  message: (_, { length }) => {
    if (length % 10 === 1) return ` ${length} `;
    else if (
      (length % 10 === 2 || length % 10 === 3 || length % 10 === 4) &&
      Math.floor(length / 10) % 10 !== 1
    )
      return ` ${length} `;
    else return ` ${length} `;
  },
});

extend("min", {
  ...rules.min,
  message: (_, { length }) => {
    if (length % 10 === 1) return ` ${length} `;
    else if (
      (length % 10 === 2 || length % 10 === 3 || length % 10 === 4) &&
      Math.floor(length / 10) % 10 !== 1
    )
      return ` ${length} `;
    else return ` ${length} `;
  },
});

extend("password", {
  params: ["target"],
  validate(value, { target }) {
    return value === target;
  },
  message: "  ",
});
